body {
  font-family: Open Sans, sans-serif;
}

.temp-span {
  font-weight: bold;
}

.loader {
  width: 64px;
  height: 64px;
  background: #fff;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  background: #222b32;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  top: 8px;
  bottom: 8px;
  left: 8px;
  right: 8px;
}

.loader:before {
  content: "";
  background: #de3500;
  margin: auto;
  animation: crlMugLoader 2s linear infinite alternate;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes crlMugLoader {
  0%, 10% {
    transform: translateY(64px);
  }

  90%, 100% {
    transform: translateY(0);
  }
}

.error {
  color: red;
  display: none;
}

.cat-image {
  width: 550px;
  height: 400px;
}

.cat-info {
  flex-direction: row;
  padding: 20px;
}

.description {
  max-width: 350px;
}

.is-hidden {
  display: none;
}

